:root {
  --text-primary-light: hsl(0, 0%, 1%);
  --text-secondary-light: hsl(0, 0%, 38%);
  --bg-primary-light: hsl(0, 0%, 98%);
  --bg-secondary-light: hsla(0, 0%, 100%, 0.9);
  --accent-light-color: hsl(0, 0%, 90%);
  --tag-bg-primary-light: hsl(0, 0%, 94%);
  --tag-bg-secondary-light: hsl(0, 0%, 97%);
  --text-primary-dark: hsl(0, 0%, 100%);
  --text-secondary-dark: hsl(0, 0%, 67%);
  --bg-primary-dark: hsl(0, 0%, 9%);
  --bg-secondary-dark: hsl(0, 0%, 13%);
  --accent-color-light: hsl(0, 0%, 28%);
  --search-box-dark: hsl(0, 0%, 7%);
  --search-icon-bg: hsl(0, 0%, 97%);
  --channel-active: hsl(214, 94%, 43%);
  --youtube-red: hsl(0, 100%, 50%);
  --border-primary: rgba(0, 0, 0, 0.1);
  --border-secondary-color: hsl(0, 0%, 80%);
}

:root {
  --font-main: "Roboto", Arial, sans-serif;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: inherit;
}

body {
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

button {
  border: none;
  background-color: unset;
}

button:hover {
  cursor: pointer;
}

body {
  font-family: var(--font-main);
}

.header {
  background-color: var(--bg-secondary-light);
}
.header .nav-toggler {
  width: 24px;
  height: 24px;
}
.header__yt-logo {
  width: 90px;
}
.header__yt-logo-letter {
  fill: hsl(0deg, 0%, 14%);
}
.header .home-logo-link {
  position: relative;
}
.header .location-label {
  font-size: 0.65rem;
  font-weight: 300;
}
.header__center .search-section {
  border: 1px solid var(--border-secondary-color);
}
.header__center .search-box__input {
  height: 40px;
  border: none;
  width: 100%;
  padding-left: 0.5em;
}
.header__center .search-box .focused-input {
  padding-left: 2em;
}
.header__center .search-box__icon {
  width: 25px;
  height: 25px;
}
.header__center .search-box__icon--s, .header__center .search-box__icon--close {
  width: 21px;
  height: 21px;
}
@media (min-width: 680px) {
  .header__center .search-box__icon--arrow {
    display: none;
  }
}
.header__center .search-suggestion {
  background-color: #fff;
  border: 1px solid var(--border-primary);
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.2);
  padding: 0.5em;
}
.header__center .search-suggestion__list {
  margin-top: 0.5em;
}
.header__center .search-suggestion__link {
  margin-bottom: 0.5em;
  position: relative;
}
.header__center .search-suggestion__link-text {
  font-weight: 500;
}
.header__center .search-suggestion__link-right {
  position: absolute;
  right: 0;
  background-color: #fff;
}
.header__center .search-suggestion__remove-btn {
  color: var(--channel-active);
  font-weight: normal;
  font-size: 13px;
}
.header__center .search-suggestion__remove-btn:hover {
  text-decoration: underline;
}
.header__center .search-btn-1 {
  height: 40px;
  background-color: var(--search-icon-bg);
  border-left: 1px solid var(--border-secondary-color);
  padding: 0.5em 1.1em 0 1.1em;
}
.header__center .mic-btn-1 {
  background-color: var(--search-icon-bg);
  padding: 0.5em;
  border-radius: 30px;
}
.header__right .search-box__icon {
  width: 18px;
  height: 18px;
}
.header__right-btn {
  height: 24px;
}
.header__right-profile-img {
  width: 32px;
  height: 32px;
  border-radius: 30px;
}
@media (min-width: 680px) {
  .header__right .search-btn-2,
.header__right .mic-btn-2 {
    display: none;
  }
}
.header .icon-hide {
  display: none;
}
.app-drawer {
  width: 230px;
  scrollbar-width: none;
}
.app-drawer:hover {
  scrollbar-width: thin;
}
@media (min-width: 1330px) {
  .app-drawer__header-section {
    display: none;
  }
}
.app-drawer__navigation-item-hide {
  display: none;
}
.app-drawer__navigation-item-box {
  padding: 0.36em 0;
  padding-left: 25px;
}
.app-drawer__navigation-item-box:hover {
  background-color: hsl(0deg, 0%, 95%);
}
.app-drawer__navigation-item-box-active {
  background-color: hsl(0deg, 0%, 90%);
}
.app-drawer__navigation-item-box-active .app-drawer__navigation-item-text {
  font-weight: 500;
}
.app-drawer__navigation-item-text {
  margin: 0;
  font-size: 14.5px;
  text-transform: capitalize;
}
.app-drawer ul {
  padding: 0.4em 0;
  border-bottom: 1px solid var(--border-primary);
}
.app-drawer__navigation-heading {
  text-transform: uppercase;
  font-size: 0.9rem;
  padding-left: 20px;
  margin: 0.5em 0;
  font-weight: 500;
  color: var(--text-secondary-light);
}
.app-drawer__navigation-item-img {
  height: 25px;
  width: 25px;
  border-radius: 30px;
}
.app-drawer__navigation-channel-dot-active {
  display: block;
  width: 5px;
  height: 5px;
  border-radius: 10px;
  background-color: var(--channel-active);
}
.app-drawer__navigation-item-channel-box:hover {
  background-color: hsl(0deg, 0%, 95%);
}
.app-drawer__footer {
  padding: 0.5em 0.8em;
}
.app-drawer__footer-links {
  font-weight: 500;
  font-size: 0.85rem;
  margin-right: 3px;
  margin-bottom: 10px;
  color: var(--text-secondary-light);
  text-transform: capitalize;
}
.app-drawer__footer-copyright {
  font-size: 0.7rem;
  color: var(--border-secondary-color);
}

.app-mini {
  display: none;
  width: 70px;
  height: 100%;
}
@media (max-width: 1330px) {
  .app-mini {
    display: block;
  }
}
@media (max-width: 680px) {
  .app-mini {
    display: none;
  }
}
.app-mini__navigation-item-box {
  display: grid;
  place-items: center;
  margin-bottom: 20px;
  padding: 0.5em 0;
}
.app-mini__navigation-item-box:hover {
  background-color: hsl(0deg, 0%, 95%);
}
.app-mini .app-icon {
  width: 22px;
  height: 22px;
}
.app-mini__navigation-item-text {
  margin: 0;
  font-size: 10px;
  text-align: center;
  text-transform: capitalize;
}

@media (max-width: 1440px) {
  .app-mini-active {
    display: block;
  }
  .app-mini-not-active {
    display: none;
  }
}

.tags {
  border-top: 1px solid var(--border-primary);
  border-bottom: 1px solid var(--border-primary);
  background-color: #fff;
}
.tags .tag {
  text-transform: capitalize;
  background-color: var(--tag-bg-primary-light);
  border: 1px solid var(--border-primary);
  padding: 0.3em 1em;
  white-space: nowrap;
  border-radius: 20px;
  margin-right: 1em;
  font-size: 15px;
}
.tags .tag-active {
  background-color: var(--text-primary-light);
  color: #fff;
}
.tags .arrow-left {
  background-color: #fff;
  margin-left: 0.5em;
}
.tags .arrow-right {
  margin-right: 0em;
  background-color: #fff;
}

.video:hover {
  cursor: pointer;
}
.video__thumbnail-box {
  background-color: var(--border-secondary-color);
}
.video__duration {
  color: #fff;
  font-size: 0.74rem;
  background-color: rgba(0, 0, 0, 0.8);
  font-weight: 500;
  border-radius: 2px;
}
.video__channel-img {
  border-radius: 30px;
  width: 15%;
}
.video .options-btn {
  width: 10%;
}
.video__description {
  width: 75%;
}
.video__title {
  font-weight: 500;
  color: var(--text-primary-light);
  line-height: 1.3;
  font-size: 0.9rem;
  width: 100%;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  overflow: hidden;
}
.video__channel-verified {
  font-size: 1.3em;
  margin-left: 0.2em;
}
.video__btns {
  display: none;
}
.video__footer, .video__channel-name {
  color: var(--text-secondary-light);
  font-size: 0.73em;
  text-transform: capitalize;
  font-weight: normal;
  margin: 0;
}
.video__btn {
  color: var(--text-secondary-light);
  text-transform: capitalize;
}
.video__btn svg {
  fill: var(--text-secondary-light);
}
.video__channel-name {
  margin-top: 0.6em;
}
.video .btns-active {
  display: none;
}

.header {
  padding: 0.7em 1em;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0;
}
.header__left {
  display: flex;
  justify-content: unset;
  align-items: center;
  gap: 20px;
  width: 230px;
  flex-shrink: 0;
}
@media (max-width: 680px) {
  .header__left {
    width: 150px;
  }
}
.header .home-logo-link {
  display: flex;
  justify-content: unset;
  align-items: unset;
  gap: 4px;
}
.header-search-main {
  width: 45%;
}
@media (max-width: 680px) {
  .header-search-main {
    display: none;
    position: absolute;
    width: 100%;
    left: 0;
    background-color: #fff;
    padding: 0.7em;
  }
}
.header__center {
  display: flex;
  justify-content: unset;
  align-items: center;
  gap: 10px;
  width: 100%;
  flex-shrink: 20;
  margin-right: 20px;
}
.header__center .search-section {
  display: flex;
  justify-content: unset;
  align-items: unset;
  gap: 0em;
  width: inherit;
  position: relative;
}
.header__center .search-box {
  display: flex;
  justify-content: unset;
  align-items: center;
  gap: 0em;
  width: inherit;
  position: relative;
}
.header__center .search-box__icon--s, .header__center .search-box__icon--close {
  position: absolute;
}
.header__center .search-box__icon--s {
  left: 5px;
}
.header__center .search-box__icon--close {
  right: 5px;
}
.header__center .search-suggestion {
  display: none;
  position: absolute;
  left: 0;
  top: 41px;
  z-index: 1000;
  width: inherit;
  min-width: 300px;
  flex-shrink: inherit;
  margin-left: -0.5px;
}
.header__center .search-suggestion__link {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0em;
}
.header__center .search-suggestion__link-left {
  display: flex;
  justify-content: unset;
  align-items: center;
  gap: 10px;
}
.header__right {
  display: flex;
  justify-content: unset;
  align-items: center;
  gap: 0em;
  gap: max(0.5em, 1.5vw);
  margin-left: 15px;
  flex-shrink: 0;
}
@media (max-width: 680px) {
  .header__right {
    flex-shrink: 1;
    margin-left: 0;
  }
}
.header__right-profile-btn {
  flex-shrink: 0;
}
@media (max-width: 450px) {
  .header__right .apps-btn,
.header__right .bell-btn {
    display: none;
  }
}
.header .location {
  position: relative;
  top: -13px;
}

main {
  display: flex;
  justify-content: unset;
  align-items: unset;
  gap: 0;
  height: 90vh;
}

aside {
  height: 100%;
}

.app-drawer-wrapper {
  height: 100%;
}
@media (max-width: 1330px) {
  .app-drawer-wrapper {
    display: none;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    top: 0;
    position: absolute;
    z-index: 500;
  }
}

.app-drawer {
  overflow-y: auto;
  background-color: #fff;
  height: 100%;
}
.app-drawer__header {
  display: flex;
  justify-content: unset;
  align-items: center;
  gap: 15px;
  padding: 1.2em 20px 0 0.8em;
  padding-left: 20px;
}
@media (min-width: 1330px) {
  .app-drawer__header {
    display: none;
  }
}
.app-drawer .yt-main-logo {
  width: 90px;
}
.app-drawer .home-logo-link {
  display: flex;
  justify-content: unset;
  align-items: center;
  gap: 0em;
}
.app-drawer__navigation-item-box {
  display: flex;
  justify-content: unset;
  align-items: center;
  gap: 20px;
}
.app-drawer__navigation-item-channel-box {
  display: flex;
  justify-content: unset;
  align-items: center;
  gap: 8px;
}

@media (max-width: 1440px) {
  .app-drawer-active {
    display: block;
  }
  .app-drawer-not-active {
    display: none;
  }
}

.contents {
  overflow-x: hidden;
  height: 100%;
}

.tags {
  height: 9%;
  display: flex;
  justify-content: unset;
  align-items: center;
  gap: 0em;
  position: relative;
  scrollbar-width: none;
}
.tags-btns {
  position: absolute;
  z-index: 10;
  width: 100%;
  display: flex;
  justify-content: unset;
  align-items: center;
  gap: 0em;
}
.tags-btn {
  display: block;
  width: 80px;
  height: 50px;
}
.tags-btn span {
  display: block;
  position: relative;
}
.tags-list {
  display: flex;
  justify-content: unset center;
  align-items: 10px;
  gap: 0em;
  --slider-index: 0;
  --slider-width:100%
  margin-left: 1em;
  width: 100%;
  z-index: 4;
  scrollbar-width: none;
  transform: translateX(calc(var(--slider-index) * -22vmax));
  transition: 250ms ease;
}
.tags #arrow-left {
  margin-right: auto;
  background-image: linear-gradient(to left, rgba(255, 255, 255, 0.5), #fff);
}
.tags #arrow-left span {
  right: 15px;
}
.tags #arrow-right span {
  left: 15px;
}
.tags #arrow-right {
  margin-left: auto;
  background-image: linear-gradient(to right, rgba(255, 255, 255, 0.5), #fff);
}
.tags .arrow-hide {
  display: none;
}

.videos-grid {
  height: 91%;
  overflow-y: scroll;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 35px 20px;
  width: 96%;
  margin-inline: auto;
  padding-top: 1.6em;
  scrollbar-width: none;
}

.video {
  width: 100%;
  max-width: 360px;
  margin-inline: auto;
}
.video__thumbnail-box {
  position: relative;
  height: 100%;
}
.video__thumbnail {
  width: 100%;
  height: 100%;
  z-index: 8;
  position: absolute;
}
.video__thumbnail:hover {
  opacity: 0;
}
.video__video {
  width: 100%;
  height: 50%;
  position: relative;
  z-index: 3;
}
.video__duration {
  position: absolute;
  bottom: 5px;
  right: 5px;
  padding: 0.07em 0.3em;
}
.video__info {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 12px;
  margin-top: 0.8em;
}
.video__channel-img {
  width: 37px;
  height: 37px;
}
.video__btns {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  flex-wrap: wrap;
  margin-top: 1.5em;
}
.video__btn {
  background-color: var(--border-primary);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  padding: 0.5em;
  width: 100%;
}